import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MyShopVuePage } from './my-shopvue/pages/my-shopvue-page/my-shopvue-page.component';

const routes: Routes = [
  {
    path: '',
    component: MyShopVuePage,
    data: { pageTitle: 'My Shopvue' },
  },
  {
    path: 'week-at-a-glance',

    loadChildren: () =>
      import('./week-at-a-glance/week-at-a-glance.module').then(
        (m) => m.WeekAtAGlanceModule
      ),
  },
  {
    path: 'manage-roles',

    loadChildren: () =>
      import('./manage-roles/manage-roles.module').then(
        (m) => m.ManageRolesModule
      ),
  },
  {
    path: 'di-authoring',

    loadChildren: () =>
      import('./di-authoring/di-authoring.module').then(
        (m) => m.DiAuthoringModule
      ),
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { enableTracing: false })],
  exports: [RouterModule],
})
export class AppRoutingModule {}
