import {
  Component,
  OnInit,
  ChangeDetectionStrategy,
  Output,
  EventEmitter,
  OnDestroy,
  ViewChild,
} from '@angular/core';
import { NavigationEnd, Router } from '@angular/router';
import { DxButtonComponent } from 'devextreme-angular';
import { Subscription } from 'rxjs';
import { filter, tap } from 'rxjs/operators';
import { NavigationService } from 'src/app/navigation.service';

@Component({
  selector: 'app-main-menu-button',
  templateUrl: './main-menu-button.component.html',
  styleUrls: ['./main-menu-button.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class MainMenuButtonComponent implements OnInit, OnDestroy {
  @Output() toggleMenu = new EventEmitter<void>();

  @Output() goBack = new EventEmitter<void>();

  private routerEventSubscription?: Subscription;

  @ViewChild(DxButtonComponent)
  backButton?: DxButtonComponent;

  constructor(
    private router: Router,
    private navigationService: NavigationService
  ) {}

  ngOnInit(): void {
    // TODO: Put this in navigation service
    this.routerEventSubscription = this.router.events
      .pipe(
        filter((event) => event instanceof NavigationEnd),
        tap((_) => {
          if (this.backButton) {
            this.backButton.disabled = !this.navigationService.canGoBack();
          }
        })
      )
      .subscribe();
  }

  ngOnDestroy(): void {
    this.routerEventSubscription?.unsubscribe();
  }
}
