import { Injectable } from '@angular/core';
import * as dayjs from 'dayjs';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root',
})
export class DateUtilsService {
  constructor() {}

  addDays(date: Date, days: number): Date {
    return dayjs(date).add(days, 'day').toDate();
  }

  stringToDate(dateString: string, dateFormat: string): Date {
    return dayjs(dateString, dateFormat).toDate();
  }

  dateToString(date: Date, dateFormat: string): string {
    return dayjs(date).format(dateFormat);
  }

  stringToDateWithFormatArray(dateString: string, dateFormat: string[]): Date {
    return dayjs(dateString, dateFormat, 'en', true).toDate();
  }

  stringToTimeWithFormatArray(dateString: string, timeFormat: string[]): Date {
    console.log('Date String: ' + dateString);
    return dayjs(dateString, timeFormat, 'en', true).toDate();
  }

  toShortDate(date: Date): Date {
    return dayjs(date).startOf('date').toDate();
  }

  getNumberOfSeconds(currentdate: Date): number {
    let startTime = new Date(currentdate);
    startTime = new Date(startTime.setHours(0, 0, 0, 0));

    const seconds = Math.floor(
      (currentdate.getTime() - startTime.getTime()) / 1000
    );
    return seconds;
  }

  isDateInArray(dateValue: Date, arrayToSearch: Array<Date>): boolean {
    for (const item of arrayToSearch) {
      if (dateValue.getTime() === item.getTime()) {
        return true;
      }
    }
    return false;
  }

  compareDate(date1: Date, date2: Date): number {
    const blankDate = new Date(0);

    // trucate dates just in case
    date1 = this.toShortDate(date1);
    date2 = this.toShortDate(date2);

    if (
      date1.getDate() === blankDate.getDate() &&
      date2.getDate() === blankDate.getDate()
    ) {
      return 0;
    }
    if (date1.getDate() === blankDate.getDate()) {
      return -1;
    }
    if (date2.getDate() === blankDate.getDate()) {
      return 1;
    }

    // If the values are not blank dates compare the two date objects to sort by the time
    if (date1.valueOf() === date2.valueOf()) {
      return 0;
    } else if (date1.valueOf() > date2.valueOf()) {
      return 1;
    } else {
      return -1;
    }
  }

  compareDateTime(date1: Date, date2: Date): number {
    const blankDate = new Date(0);

    if (
      (date1 === undefined || date1.getDate() === blankDate.getDate()) &&
      (date2 === undefined || date2.getDate() === blankDate.getDate())
    ) {
      return 0;
    }
    if (date1 === undefined || date1.getDate() === blankDate.getDate()) {
      return -1;
    }
    if (date2 === undefined || date2.getDate() === blankDate.getDate()) {
      return 1;
    }

    // If the values are not blank dates compare the two date objects to sort by the time
    if (date1.valueOf() === date2.valueOf()) {
      return 0;
    } else if (date1.valueOf() > date2.valueOf()) {
      return 1;
    } else {
      return -1;
    }
  }

  sameDay(date1?: Date, date2?: Date): boolean {
    if (date1 === undefined || date2 === undefined) {
      return false;
    } else {
      return dayjs(date1).isSame(dayjs(date2), 'day');
    }
  }

  getMostRecentStartOfCycle(
    asOfDate: Date,
    cycleStartDate: Date,
    cycleLength: number
  ): Date {
    if (cycleLength === 0) {
      return cycleStartDate;
    }
    const daysSinceStartOfCycle = dayjs(asOfDate).diff(cycleStartDate, 'day');
    const lestMultipleOfCycleLength =
      cycleLength * Math.floor(daysSinceStartOfCycle / cycleLength);
    return dayjs(asOfDate)
      .add(lestMultipleOfCycleLength - daysSinceStartOfCycle, 'day')
      .toDate();
  }

  today(): Date {
    let result = this.toShortDate(new Date());
    result = new Date(result.toISOString().replace('Z', ''));
    return result;
  }

  now(): Date {
    let result = new Date();
    result = new Date(result.toISOString());
    return result;
  }
}

// eslint-disable-next-line @typescript-eslint/no-explicit-any
export const normalizeDateProperties = (obj: any): any => {
  for (const k in obj) {
    if (typeof obj[k] === 'object' && obj[k] !== null) {
      if (obj[k] instanceof Date) {
        // TODO: Temporary hack to get a non UTC date
        // with 0 hours minutes and seconds
        const localDate = new Date(
          obj[k].valueOf() - obj[k].getTimezoneOffset() * 60000
        );
        obj[k] = localDate.toISOString().slice(0, -1);
      } else {
        normalizeDateProperties(obj[k]);
      }
    }
  }
  return obj;
};

// eslint-disable-next-line @typescript-eslint/no-explicit-any
export const stringPropertiesToDates = <T>() =>
  map<T, T>((x) => {
    iso8601StringPropertiesToDate(x);
    return x;
  });

const iso8601 = /^\d{4}-\d\d-\d\dT\d\d:\d\d:\d\d(\.\d+)?(([+-]\d\d:\d\d)|Z)?$/;

// eslint-disable-next-line @typescript-eslint/no-explicit-any
const iso8601StringPropertiesToDate = (obj: any) => {
  for (const k in obj) {
    if (typeof obj[k] === 'object' && obj[k] !== null) {
      iso8601StringPropertiesToDate(obj[k]);
    } else if (typeof obj[k] === 'string') {
      if (iso8601.test(obj[k])) {
        obj[k] = new Date(obj[k]);
      }
    }
  }
};

export type DayOfWeekIndex = 0 | 1 | 2 | 3 | 4 | 5 | 6;
