import { ApplicationRef, Injectable, OnDestroy, OnInit } from '@angular/core';
import { SwUpdate } from '@angular/service-worker';
import { concat, interval, Subscription } from 'rxjs';
import { first, switchMap } from 'rxjs/operators';
import { AppUpdateDialogService } from './app-update-dialog.service';

@Injectable({
  providedIn: 'root',
})
export class AppUpdateService implements OnDestroy {
  private subscriptions = new Subscription();

  constructor(
    appRef: ApplicationRef,
    updates: SwUpdate,
    appUpdateDialogService: AppUpdateDialogService
  ) {
    // Only if service worker is available
    if (updates.isEnabled) {
      // Poll for updates
      const appIsStable$ = appRef.isStable.pipe(
        first((isStable) => isStable === true)
      );
      // TODO: 10 seconds for debugging, make this configurable?
      const updateCheckInterval$ = interval(10 * 1000);
      const updateCheckIntervalOnceAppIsStable$ = concat(
        appIsStable$,
        updateCheckInterval$
      );
      this.subscriptions.add(
        updateCheckIntervalOnceAppIsStable$.subscribe(() =>
          updates.checkForUpdate()
        )
      );

      // Inform user about available update
      this.subscriptions.add(
        updates.available
          .pipe(switchMap((event) => appUpdateDialogService.available(event)))
          .subscribe((value) => {
            if (value) updates.activateUpdate();
          })
      );

      // Inform user that update was applied
      this.subscriptions.add(
        updates.activated
          .pipe(switchMap((event) => appUpdateDialogService.activated(event)))
          .subscribe(
            // Reload page here.
            // If we do it in upates.available, the activated dialog won't show.
            (e) => document.location.reload()
          )
      );

      // Inform user that the update is unrecoverable and the page needs to be reloaded.
      // TODO: Can we just reload the page programatically?
      // Hard to test this one!
      this.subscriptions.add(
        updates.unrecoverable
          .pipe(
            switchMap((event) => appUpdateDialogService.unrecoverable(event))
          )
          .subscribe()
      );
    }
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }
}
