import { BrowserModule, Title } from '@angular/platform-browser';
import { APP_INITIALIZER, LOCALE_ID, NgModule } from '@angular/core';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { SharedModule } from './shared/shared.module';
import { CoreModule } from './core/core.module';
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { PendingChangesGuard } from './pending-changes.guard';
import { ConfirmDialogService } from './confirmDialog.service';
import { ServiceWorkerModule } from '@angular/service-worker';
import { environment } from '../environments/environment';
import { AppUpdateDialogService } from './core/services/app-update-dialog.service';
import localeEses from '@angular/common/locales/es';
import { registerLocaleData } from '@angular/common';
import { DragDropDirective } from './drag-drop.directive';

registerLocaleData(localeEses);

@NgModule({
  declarations: [AppComponent, DragDropDirective],
  imports: [
    BrowserModule,
    AppRoutingModule,
    CoreModule,
    SharedModule,
    BrowserAnimationsModule,
    FontAwesomeModule,
    ServiceWorkerModule.register('ngsw-worker.js', {
      enabled: environment.production,
      // Register the ServiceWorker as soon as the app is stable
      // or after 30 seconds (whichever comes first).
      registrationStrategy: 'registerWhenStable:30000',
    }),
  ],
  exports: [BrowserAnimationsModule],
  providers: [
    PendingChangesGuard,
    ConfirmDialogService,
    AppUpdateDialogService,
  ],

  bootstrap: [AppComponent],
})
export class AppModule {}

