import { Injectable } from '@angular/core';
import {
  UnrecoverableStateEvent,
  UpdateActivatedEvent,
  UpdateAvailableEvent,
} from '@angular/service-worker';
import { custom } from 'devextreme/ui/dialog';
import { from, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class AppUpdateDialogService {
  constructor() {}

  available(event: UpdateAvailableEvent | undefined): Observable<boolean> {
    let buttons = [
      {
        text: 'Update Now',
        onClick: (e: any) => {
          return true;
        },
      },
    ];

    let updateAvailableDialog = custom({
      title: 'Update Detected',
      messageHtml: 'An update is available and will be applied in N seconds.',
      buttons,
    });

    return from(updateAvailableDialog.show()) as Observable<boolean>;
  }

  activated(event: UpdateActivatedEvent | undefined): Observable<boolean> {
    let buttons = [
      {
        text: 'OK',
        onClick: (e: any) => {
          return true;
        },
      },
    ];

    let updateActivatedDialog = custom({
      title: 'Update Completed',
      // TODO: Add 'You have been updated to the version XYZ.'
      messageHtml: 'You have been updated to the current version.',
      buttons,
    });

    return from(updateActivatedDialog.show()) as Observable<boolean>;
  }

  unrecoverable(
    event: UnrecoverableStateEvent | undefined
  ): Observable<boolean> {
    let buttons = [
      {
        text: 'OK',
        onClick: (e: any) => {
          return true;
        },
      },
    ];

    let updateUnrecoverableDialog = custom({
      title: 'Automatic Update Incomplete',
      messageHtml:
        'The automatic update was unable to complete on its own.<br/>Please do a Refresh in the browser to complete the update.',
      buttons,
    });

    return from(updateUnrecoverableDialog.show()) as Observable<boolean>;
  }
}
