import { Component, ViewChild, OnInit } from '@angular/core';
import { DxDrawerComponent } from 'devextreme-angular';
import { AppUpdateService } from './core/services/app-update.service';
import { NavigationService } from './navigation.service';
import { ShopvueTitleService } from './shopvue-title.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})
export class AppComponent implements OnInit {
  appTitle = 'ShopVueRoutingDemo';
  opened = false;

  appDatabase = 'SV_Open';

  menuItems = [
    {
      name: 'Home',
      link: '',
      icon: 'home',
    },
    {
      name: 'Manage Roles',
      link: 'manage-roles',
      icon: 'settings',
    },
    {
      name: 'Week At A Glance',
      link: 'week-at-a-glance',
      icon: 'today',
    },
    {
      name: 'DI Authoring',
      link: 'di-authoring',
      icon: 'article',
    },
  ];

  @ViewChild(DxDrawerComponent, { static: false }) drawer?: DxDrawerComponent;

  constructor(
    private navigationService: NavigationService,
    private shopvueTitleService: ShopvueTitleService,
    private appUpdateService: AppUpdateService
  ) {}

  ngOnInit(): void {}

  toggleMenu(): void {
    if (!this.opened) {
      this.drawer?.instance.show();
    } else {
      this.drawer?.instance.hide();
    }
  }

  navigateBack(): void {
    this.navigationService.goBack();
  }

  hideMenu(): void {
    this.drawer?.instance.hide();
  }
}
