import { Injectable, OnDestroy } from '@angular/core';
import { Location } from '@angular/common';
import { BehaviorSubject, Subscription } from 'rxjs';
import { NavigationEnd, Router } from '@angular/router';
import { filter, tap } from 'rxjs/operators';

@Injectable({
  providedIn: 'root',
})
export class NavigationService implements OnDestroy {
  private routerEventSubscription?: Subscription;

  public backButtonEnabled = new BehaviorSubject(false);

  constructor(private location: Location, private router: Router) {
    this.routerEventSubscription = this.router.events
      .pipe(
        filter((event) => event instanceof NavigationEnd),
        tap((_) => {
          this.backButtonEnabled.next(!this.canGoBack());
        })
      )
      .subscribe();
  }

  canGoBack = () => !this.location.isCurrentPathEqualTo('');

  goBack(): void {
    if (this.canGoBack()) {
      // TODO: Use browser history
      this.location.back();

      /*
      // TODO: Back to parent, trim off last path segment
      const parts = this.router.url.split('/');
      parts.pop();
      const url = parts.join('/');
      this.router.navigateByUrl(url);
      */
    }
  }

  ngOnDestroy(): void {
    this.routerEventSubscription?.unsubscribe();
  }
}
