import { AfterViewChecked, Component, OnInit } from '@angular/core';
import { AppUpdateDialogService } from 'src/app/core/services/app-update-dialog.service';

@Component({
  selector: 'app-my-shopvue',
  templateUrl: './my-shopvue-page.component.html',
  styleUrls: ['./my-shopvue-page.component.scss'],
})
export class MyShopVuePage implements OnInit, AfterViewChecked {
  value: string = '';

  constructor(private appUpdateDialogService: AppUpdateDialogService) {}

  ngOnInit(): void {}

  ngAfterViewChecked(): void {}

  updateAvailable() {
    this.appUpdateDialogService.available(undefined);
  }

  updateActivated() {
    this.appUpdateDialogService.activated(undefined);
  }

  updateUnrecoverable() {
    this.appUpdateDialogService.unrecoverable(undefined);
  }
}
